.aboutus {
  background-image: url(../Assets/componentBG/bg1.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center center;
  height: 100vh;
  clip-path: polygon(0 0, 100% 0, 100% 60%, 0 90%);
}

.txtshdw {
  font-size: calc(2vw + 2vh);
  text-shadow: 1px 1px 2px rgb(0, 0, 0), 0 0 1em blue, 0 0 0.2em blue;
  /* text-shadow: rgb(4, 102, 248) 1px 0 10px; */
}

/* mentor and team css  */
@property --gap {
  syntax: "<length>";
  inherits: true;
  initial-value: 0;
}
@property --angle {
  syntax: "<angle>";
  inherits: true;
  initial-value: 0deg;
}
.imgclr1 {
  --border: 15px;
  --gap: 12px;
  --color: #5990e2;
}
.imgclr2 {
  --border: 15px;
  --gap: 12px;
  --color: #e96e99;
}
.imgclr3 {
  --border: 15px;
  --gap: 12px;
  --color: #d8e379;
}
.imgclr4 {
  --border: 15px;
  --gap: 12px;
  --color: #e6a475;
}
.imagesmember {
  /* --border: 15px;
    --gap: 12px;
    --color: #FA6900; */

  width: 200px;
  aspect-ratio: 1;
  box-sizing: border-box;
  border-radius: 50%;
  margin: 20px;
  cursor: pointer;
  padding: calc(var(--border) + var(--gap));

  --angle: 90deg;
  background: radial-gradient(farthest-side, var(--color) 97%, #0000 101%)
      85.35% 85.35%,
    conic-gradient(
      from calc(180deg - var(--angle) / 2),
      #0000 var(--angle),
      var(--color) 0
    ),
    radial-gradient(farthest-side, var(--color) 97%, #0000 101%) 14.65% 85.35%;
  background-size: var(--border) var(--border), auto;
  background-repeat: no-repeat;

  -webkit-mask: radial-gradient(
    farthest-side,
    #000 calc(99% - calc(var(--border) + var(--gap))),
    #0000 calc(100% - calc(var(--border) + var(--gap)))
      calc(99% - var(--border)),
    #000 calc(100% - var(--border))
  );
  mask: radial-gradient(
    farthest-side,
    #000 calc(99% - calc(var(--border) + var(--gap))),
    #0000 calc(100% - calc(var(--border) + var(--gap)))
      calc(99% - var(--border)),
    #000 calc(100% - var(--border))
  );
  transition: --angle 0.4s, --gap 0.4s;
}
.imagesmember:hover {
  --angle: 0deg;
  --gap: 5px;
}

